import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SmartTableService} from '../../../@core/data/smart-table.service';
import {MARKERPROP, MarketProp, Phone, PHONES} from './market';
import {MultiService} from '../multi.service';

@Component({
  selector: 'ngx-market',
  templateUrl: './market.component.html',
  styleUrls: ['../multi.component.scss'],
})
export class MarketComponent implements OnInit {
  options: any = {};
  phones: Phone[] = PHONES;
  mInstance: any;
  markerData: MarketProp = MARKERPROP;

  constructor(private service: SmartTableService, multiService: MultiService) {
    multiService.changeTitle('APP市场占有率');
    const data = this.service.getData();
    this.source.load(data);
  }

  chartInit(ev): void {
    this.mInstance = ev;
    this.mInstance.on('updateAxisPointer', (event) => {
      const xAxisInfo = event.axesInfo[0];
      if (xAxisInfo) {
        const dimension = xAxisInfo.value + 1;
        ev.setOption({
          series: {
            id: 'pie',
            label: {
              formatter: '{b}: {@[' + dimension + ']} ({d}%)',
            },
            encode: {
              value: dimension,
              tooltip: dimension,
            },
          },
        });
      }
    });
  }

  ngOnInit() {
    this.options = {
      legend: {},
      tooltip: {
        trigger: 'axis',
        showContent: false,
      },
      dataset: {
        source: this.markerData.detailData,
      },
      xAxis: {type: 'category'},
      yAxis: {gridIndex: 0},
      grid: {top: '55%'},
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [
        {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        {type: 'line', smooth: true, seriesLayoutBy: 'row'},
        {
          type: 'pie',
          id: 'pie',
          radius: '30%',
          center: ['50%', '25%'],
          label: {
            formatter: '{b}: {@2018/03/01} ({d}%)',
          },
          encode: {
            itemName: this.markerData.firstData[0],
            value: this.markerData.firstData[1],
            tooltip: this.markerData.firstData[1],
          },
        },
      ],
    };
  }


  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



