import {AfterViewInit, Component} from '@angular/core';
import {DataCompare, DATACOMPARE} from '../compare/compare';

@Component({
  selector: 'ngx-multi-compare',
  template: `
    <div echarts [options]="options" theme="light" class="echart"></div>
  `,
})
export class MultiCompareComponent implements AfterViewInit {
  options: any = {};
  dataCompare: DataCompare = DATACOMPARE;

  ngAfterViewInit() {
    this.options = {
      tooltip: {
        trigger: 'axis',
      },
      legend: {
        x: 100,
        top: '7%',
        textStyle: {
          color: '#ffd285',
        },
        data: this.dataCompare.xdata,
      },
      grid: {
        left: '2%',
        right: '2%',
        top: '16%',
        bottom: '6%',
        containLabel: true,
      },
      xAxis: {
        type: 'category',
        'axisTick': {
          'show': false,
        },
        boundaryGap: false,
        data: this.dataCompare.data,
      },
      yAxis: {
        splitLine: {
          show: true,
        },
        'axisTick': {
          'show': false,
        },
        type: 'value',
        axisLabel: {
          formatter: '{value} 人',
        },
      },
      series: [{
        name: this.dataCompare.xdata[0],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[0],
      }, {
        name: this.dataCompare.xdata[1],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[1],
      }, {
        name: this.dataCompare.xdata[2],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[2],
      }, {
        name: this.dataCompare.xdata[3],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[3],
      }, {
        name: this.dataCompare.xdata[4],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[4],
      }],
    };
  }
}



