import {AfterViewInit, Component} from '@angular/core';
import {PAGEVIEW, PageView} from '../visit/visit';

@Component({
  selector: 'ngx-multi-visit',
  template: `
    <div echarts [options]="options" theme="light" class="echart"></div>
  `,
})
export class MultiVisitComponent implements AfterViewInit {
  options: any = {};
  pageView: PageView = PAGEVIEW;

  ngAfterViewInit() {
    this.options = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow',
        },
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true,
      },
      legend: {
        data: ['APP访问量'],
      },
      xAxis: {
        type: 'value',
        boundaryGap: [0, 0.01],
      },
      yAxis: {
        type: 'category',
        data: this.pageView.yData,
      },
      series: [
        {
          name: 'APP访问量',
          type: 'bar',
          label: {
            normal: {
              show: true,
              position: 'insideRight',
            },
          },
          data: this.pageView.detailData,
        },
      ],
    };

  }
}



