import {AfterViewInit, Component} from '@angular/core';
import {TypeProp, TYPEPROP} from '../type/type';

@Component({
  selector: 'ngx-multi-type',
  template: `
    <div echarts [options]="options" theme="light" class="echart"></div>
  `,
})
export class MultiTypeComponent implements AfterViewInit {
  options: any = {};
  typeData: TypeProp = TYPEPROP;

  ngAfterViewInit() {
    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {d}%',
      },
      legend: {
        x: 'center',
        y: 'bottom',
        data: this.typeData.data,
      },
      calculable: true,
      series: [
        {
          name: '面积模式',
          type: 'pie',
          radius: [30, 110],
          center: ['50%', 200],
          roseType: 'area',
          x: '50%',               // for funnel
          max: 40,                // for funnel
          sort: 'ascending',     // for funnel
          data: this.typeData.detailData,
        },
      ],
    };
  }
}



