import {NgModule} from '@angular/core';
import {NgxEchartsModule} from 'ngx-echarts';


import {ThemeModule} from '../../@theme/theme.module';
import {TabsModule} from 'ngx-bootstrap';
import {MultiComponent} from './multi.component';
import {MultiRoutingModule} from './multi-routing.module';
import {DashboardComponent} from './dashboard/dashboard.component';
import {SmartTableService} from '../../@core/data/smart-table.service';
import {Ng2SmartTableModule} from 'ng2-smart-table';
import {VisitComponent} from './visit/visit.component';
import {MultiVisitComponent} from './dashboard/multi-visit.component';
import {MultiMarketComponent} from './dashboard/multi-market.component';
import {MultiCompareComponent} from './dashboard/multi-compare.component';
import {MultiTypeComponent} from './dashboard/multi-type.component';
import {TypeComponent} from './type/type.component';
import {CompareComponent} from './compare/compare.component';
import {MarketComponent} from './market/market.component';
import {MultiService} from './multi.service';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown/angular2-multiselect-dropdown';
import {FormsModule} from '@angular/forms';

const homes = [
  MultiVisitComponent,
  MultiMarketComponent,
  MultiCompareComponent,
  MultiTypeComponent,
];

const components = [
  MultiComponent,
  DashboardComponent,
  VisitComponent,
  TypeComponent,
  CompareComponent,
  MarketComponent,
];

@NgModule({
  imports: [ThemeModule, MultiRoutingModule, NgxEchartsModule, Ng2SmartTableModule, TabsModule.forRoot(), AngularMultiSelectModule, FormsModule],
  declarations: [...components, ...homes],
  providers: [
    SmartTableService,
    MultiService,
  ],
})
export class MultiModule {
}



