import {AfterViewInit, Component} from '@angular/core';
import {MARKERPROP, MarketProp} from '../market/market';

@Component({
  selector: 'ngx-multi-market',
  template: `
    <div echarts [options]="options" theme="light" class="echart"></div>
  `,
})
export class MultiMarketComponent implements AfterViewInit {
  options: any = {};
  markerData: MarketProp = MARKERPROP;

  ngAfterViewInit() {

    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        left: 'left',
        data: this.markerData.xdata,
      },
      series: [
        {
          name: '市场占有率',
          type: 'pie',
          radius: '55%',
          center: ['50%', '60%'],
          data: this.markerData.data,
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            },
          },
        },
      ],
    };
  }
}



