import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SmartTableService} from '../../../@core/data/smart-table.service';
import {Phone, PHONES, TYPEPROP, TypeProp} from './type';
import {MultiService} from '../multi.service';

@Component({
  selector: 'ngx-type',
  templateUrl: './type.component.html',
  styleUrls: ['../multi.component.scss'],
})
export class TypeComponent implements OnInit {
  options: any = {};
  phones: Phone[] = PHONES;
  typeData: TypeProp = TYPEPROP;

  constructor(private service: SmartTableService, multiService: MultiService) {
    multiService.changeTitle('各类型APP占比');
    const data = this.service.getData();
    this.source.load(data);
  }

  ngOnInit() {
    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {d}%',
      },
      legend: {
        x: 'center',
        y: 'bottom',
        data: this.typeData.data,
      },
      toolbox: {
        show: true,
        feature: {
          mark: {show: true},
          dataView: {show: true, readOnly: false},
          magicType: {
            show: true,
            type: ['pie', 'funnel'],
          },
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      calculable: true,
      series: [
        {
          name: '类型占比',
          type: 'pie',
          radius: [30, 110],
          center: ['50%', 200],
          roseType: 'area',
          x: '50%',               // for funnel
          max: 40,                // for funnel
          sort: 'ascending',     // for funnel
          data: this.typeData.detailData,
        },
      ],
    };
  }


  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



