import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SmartTableService} from '../../../@core/data/smart-table.service';
import {DATACOMPARE, DataCompare, Phone, PHONES} from './compare';
import {MultiService} from '../multi.service';


@Component({
  selector: 'ngx-compare',
  templateUrl: './compare.component.html',
  styleUrls: ['../multi.component.scss'],
})
export class CompareComponent implements OnInit {
  options: any = {};
  phones: Phone[] = PHONES;
  dataCompare: DataCompare = DATACOMPARE;

  constructor(private service: SmartTableService, multiService: MultiService) {
    multiService.changeTitle('APP数据对比');
    const data = this.service.getData();
    this.source.load(data);
  }

  ngOnInit() {

    this.options = {
      title: [/*{
        text: '用户人数分析',
        left: '1%',
        top: '6%',
      }, */],
      tooltip: {
        trigger: 'axis',
      },
      legend: {
        x: 300,
        top: '7%',
        textStyle: {
          color: '#ffd285',
        },
        data: this.dataCompare.xdata,
      },
      grid: {
        left: '2%',
        right: '4%',
        top: '16%',
        bottom: '6%',
        containLabel: true,
      },
      toolbox: {
        'show': false,
        feature: {
          saveAsImage: {},
        },
      },
      xAxis: {
        type: 'category',
        'axisTick': {
          'show': false,
        },
        boundaryGap: false,
        data: this.dataCompare.data,
      },
      yAxis: {
        splitLine: {
          show: true,
        },
        'axisTick': {
          'show': false,
        },
        type: 'value',
        axisLabel: {
          formatter: '{value} 人',
        },
      },
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [{
        name: this.dataCompare.xdata[0],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[0],
      }, {
        name: this.dataCompare.xdata[1],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[1],
      }, {
        name: this.dataCompare.xdata[2],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[2],
      }, {
        name: this.dataCompare.xdata[3],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[3],
      }, {
        name: this.dataCompare.xdata[4],
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.dataCompare.detailData[4],
      }],
    };
  }


  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



