import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SmartTableService} from '../../../@core/data/smart-table.service';
import {PAGEVIEW, PageView, Phone, PHONES} from './visit';
import {MultiService} from '../multi.service';

@Component({
  selector: 'ngx-visit',
  templateUrl: './visit.component.html',
  styleUrls: ['../multi.component.scss'],
})
export class VisitComponent implements OnInit {
  options: any = {};
  phones: Phone[] = PHONES;
  pageView: PageView = PAGEVIEW;

  constructor(private service: SmartTableService, multiService: MultiService) {
    multiService.changeTitle('热门APP访问量');
    const data = this.service.getData();
    this.source.load(data);
  }

  ngOnInit() {
    this.options = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow',
        },
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true,
      },
      legend: {
        data: ['APP访问量'],
      },
      toolbox: {
        show: true,
        feature: {
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      xAxis: {
        type: 'value',
        boundaryGap: [0, 0.01],
      },
      yAxis: {
        type: 'category',
        data: this.pageView.yData,
      },
      series: [
        {
          name: 'APP访问量',
          type: 'bar',
          label: {
            normal: {
              show: true,
              position: 'insideRight',
            },
          },
          data: this.pageView.detailData,
        },
      ],
    };
  }


  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



