import {Component, OnInit} from '@angular/core';
import * as moment from 'moment';
import {MultiService} from './multi.service';

declare var $;

@Component({
  selector: 'ngx-demo',
  templateUrl: './multi.component.html',
  styleUrls: ['./multi.component.scss'],
})

export class MultiComponent implements OnInit {
  title: string = '首页';
  dates: string[] = [
    '全部', '社交', '游戏', '工具', '购物', '音乐', '新闻',
  ];
  itemList = [];
  selectedItems = [];
  settings = {};

  constructor(multiService: MultiService) {
    multiService.getTitleSubject().subscribe(title => this.title = title);
  }

  ngOnInit() {
    this.dateRangePicker();
    this.itemList = [
      {'id': 1, 'itemName': '微信'},
      {'id': 2, 'itemName': 'QQ'},
      {'id': 3, 'itemName': '淘宝'},
      {'id': 4, 'itemName': '王者荣耀'},
      {'id': 5, 'itemName': '网易云音乐'},
      {'id': 6, 'itemName': '相机'},
      {'id': 7, 'itemName': '支付宝'},
      {'id': 8, 'itemName': '相机'},
      {'id': 9, 'itemName': '图库'},
    ];
  /*  this.selectedItems = [
      {'id': 1, 'itemName': '王者荣耀'},
      {'id': 2, 'itemName': '支付宝'},
      {'id': 3, 'itemName': '淘宝'},
      {'id': 4, 'itemName': '图库'}];*/
    this.settings = {
      singleSelection: false,
      text: '各种APP对比选择',
      enableCheckAll: false,
      limitSelection: 5,
      enableSearchFilter: true,
      searchPlaceholderText: 'APP搜索',
      badgeShowLimit: 3,
      classes: 'myclass custom-class-example',
    };
  }

  onItemSelect(item: any) {
    /*console.log(item);*/
    console.log(this.selectedItems);
  }

  OnItemDeSelect(item: any) {
   /* console.log(item);*/
    console.log(this.selectedItems);
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        fromLabel: '开始',
        format: 'YYYY/MM/DD',
        toLabel: '到',
        customRangeLabel: '自定义时间',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
      },
      maxDate: moment(),
      // minDate: moment().subtract(90, 'days'),
      'ranges': {
        '今日': [
          moment(),
          moment(),
        ],
        '昨日': [
          moment().subtract(1, 'days'),
          moment().subtract(1, 'days'),
        ],
        '本周': [moment().subtract(7, 'days'), moment().subtract(1, 'days')],
        '最近30天': [moment().subtract(30, 'days'), moment().subtract(1, 'days')],
        '本月': [moment().startOf('month'), moment().endOf('month')],
        '上个月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
      },
      'alwaysShowCalendars': true,
      'showCustomRangeLabel': false,
    };
    picker.daterangepicker(dataRageOption, function (start, end, label) {
      console.info(`start:${start.format('YYYY/MM/DD')}, end:${end}, label:${label}`);
    });

  }
}



